<template>
  <div class="nav">
    <h2>{{text}}</h2>
    <ul>
      <li>
        <router-link :to="{ name: 'Chercheur'}" data-text="Ajout chercheur" :class="{active: getNavActive==='chercheur'}">Ajout chercheur</router-link>
      </li>
      <li>
        <router-link :to="{ name: 'NomOuvrage'}" data-text="Ajout nom ouvrage" :class="{active: getNavActive==='nom'}">Ajout nom ouvrage</router-link>
      </li>
      <li>
        <router-link :to="{ name: 'TypeOuvrage'}" data-text="Ajout type ouvrage" :class="{active: getNavActive==='type'}">Ajout type ouvrage</router-link>
      </li>
      <li>
        <router-link :to="{ name: 'Ouvrage'}" data-text="Ajout ouvrage" :class="{active: getNavActive==='ouvrage'}">Ajout ouvrage</router-link>
      </li>
      <li>
        <router-link :to="{ name: 'Publication'}" data-text="Ajout publication" :class="{active: getNavActive==='publication'}">Ajout publication</router-link>
      </li>
      <li>
        <router-link :to="{ name: 'Systeme'}" data-text="Ajout systeme" :class="{active: getNavActive==='systeme'}">Ajout systeme</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Nav",
  computed:{
    text(){
      switch(this.$route.path){
        case '/chercheur': return 'Ajout chercheur'
        case '/nom-ouvrage': return 'Ajout nom ouvrage'
        case '/type-ouvrage': return 'Ajout type ouvrage'
        case '/ouvrage': return 'Ajout ouvrage'
        case '/publication': return 'Ajout publication'
        case '/systeme': return 'Ajout systeme'
        default: return 'Acceuil'
      }
    },
    getNavActive(){
      switch(this.$route.path){
        case '/chercheur': return 'chercheur'
        case '/nom-ouvrage': return 'nom'
        case '/type-ouvrage': return 'type'
        case '/ouvrage': return 'ouvrage'
        case '/publication': return 'publication'
        case '/systeme': return 'systeme'
        default: return 'Acceuil'
      }
    }
  }
};
</script>

<style scoped>
.nav {
  position: fixed;
  top: 0;
  left: 0;
  height: 8rem;
  width: 100vw;
  background: var(--nav-background);
  box-shadow: 0 2px 25px rgba(0, 0, 0, 0.4);
  z-index: 10;
}
.nav h2{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%) rotateX(0deg) translateY(0px);
  opacity: 1;
  color: #fff;
  transition: 0.5s;
  
}
.nav ul{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  transform: rotateX(90deg) translateY(-5px);
  opacity: 0;
  transition: 0.5s;
  
}
.nav:hover ul{
  transform: rotateX(0deg) translateY(0px);
  opacity: 1;
  transition-delay: 0.5s;
}
.nav:hover h2{
  transform: translate(-50%,-50%) rotateX(90deg) translateY(-5px);
  opacity: 0;
}
.nav ul li{
  position: relative;
  list-style: none;
}
.nav ul li a{
  text-decoration: none;
  color: rgba(255, 255, 255, 0.5);
  font-size: 1.6rem;
}
.nav ul li a::before{
  content: '';
  width: 100%;
  height: 0.2rem;
  background: var(--nav-active);
  position: absolute;
  bottom: -0.5rem;
  left: 0;
  border-radius: 2px;
  transition: 0.5s;
  transform-origin: bottom left;
  transform: scaleX(0);
}
.nav ul li:hover a{
  color: rgba(255, 255, 255, 1);
}
.nav ul li:hover a::before{
  transform: scaleX(1);
}
.nav ul li a.active::before{
  transform: scaleX(1);
}
</style>