import Vue from 'vue'
import VueRouter from 'vue-router'
import Chercheur from '../views/Chercheur'
import NomOuvrage from '../views/NomOuvrage'
import TypeOuvrage from '../views/TypeOuvrage'
import Ouvrage from '../views/Ouvrage'
import Publication from '../views/Publication'
import Systeme from '../views/Systeme'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: { name: 'Chercheur' }
  },
  {
    path: '/chercheur',
    name: 'Chercheur',
    component: Chercheur
  },  
  {
    path: '/nom-ouvrage',
    name: 'NomOuvrage',
    component: NomOuvrage
  },
  {
    path: '/type-ouvrage',
    name: 'TypeOuvrage',
    component: TypeOuvrage
  },
  {
    path: '/ouvrage',
    name: 'Ouvrage',
    component: Ouvrage
  },
  {
    path: '/publication',
    name: 'Publication',
    component: Publication
  },
  {
    path: '/systeme',
    name: 'Systeme',
    component: Systeme
  },
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})

export default router
