<template>
  <div class="publication">
    <div class="photo">
      <img :src="getphoto">
    </div>
      <form method="post" enctype="multipart/form-data">
        <div class="inputBox">
              <div>
                  <input type="text" required autocomplete="off">
                  <span>Titre</span>
                  <span class="slider"></span>
              </div>
        </div>
        <div class="inputBox textarea">
              <div>
                  <textarea name="resumer" id="resumer" placeholder="Résumer de la publication" autocomplete="off"></textarea>
              </div>
        </div>
        <div class="file">
          <input type="file" id="file" name="myFile" ref="fileInput" @change="photoChange" accept="image/jpeg, image/png, image/jpg">
        </div>
        <div class="file-1">
          <button type="button" @click="$refs.fileInput.click()">Choose a photo</button>
          <button type="button" @click="supprimerPhoto">Delete photo</button>
        </div>
        <div class="select">
            <label for="type">Nom d'Ouvrage</label>
            <select name="type" id="type-select">
                <option value="conference">Conférence</option>
                <option value="journal ">Journal </option>
                <option value="livre">Livre</option>
            </select>
        </div>
        <div class="inputBox number">
              <div>
                  <label for="pages">Pages</label>
                  <input id="pages" name="pages" type="number" required>
              </div>
        </div>
        <div class="inputBox">
              <div>
                  <input type="text" required autocomplete="off">
                  <span>Numéro</span>
                  <span class="slider"></span>
              </div>
        </div>
        <div class="inputBox">
              <div>
                  <input type="text" required autocomplete="off">
                  <span>Volume</span>
                  <span class="slider"></span>
              </div>
        </div>
        <div class="inputBox">
              <div>
                  <input type="url" required placeholder="Lien d'article">
                  <span></span>
                  <span class="slider"></span>
              </div>
        </div>
        <div class="file-1">
          <button type="submit">Valider</button>
        </div>
      </form>
  </div>
</template>

<script>
export default {
  name: "Publication",
  data() {
    return {
      photo: null,
    };
  },
  computed: {
    getphoto() {
      if (this.photo) {
        let src = URL.createObjectURL(this.photo);
        return src;
      } else {
        return "https://www.flaticon.com/svg/static/icons/svg/3342/3342137.svg";
      }
    },
  },
  methods: {
    photoChange(e){
      this.photo=e.target.files[0];
    },
    supprimerPhoto(){
      this.photo=null;
    }
  },
  mounted(){
    document.title = "Ajout Publication";
  }
};
</script>
<style scoped>
.publication{
  width: 50rem;
  height: 100%;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid rgba(0, 0, 0, 0.3);
}
.photo{
  width: 20rem;
  height: 20rem;
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.photo img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.publication form{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.textarea{
  width: 100%;
}
textarea{
  width: 100%;
  height: 10rem;
  resize: none;
  outline: none;
  font-size: 1.6rem;
  padding-left: 5px;
  padding-top: 5px;
}
.file{
  display: none;
}
.file-1{
  margin: 1.5rem 0;
  font-size: 1.6rem;
}
.file-1 button{
  margin: 0 1rem;
}
.inputBox{
  margin-top: 1.5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputBox div{
    position: relative;
    width: 80%;
}
.inputBox div input{
    width: 100%;
    padding: 5px 0;
    font-size: 18px;
    font-weight: 400;
    color: #333;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-bottom: 1px solid #777;
    outline: none;
}
.inputBox div span{
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 18px;
    font-weight: 300;
    color: #333;
    transition: 0.25s;
    pointer-events: none;
}
.inputBox div .slider{
    content: '';
    position: absolute;
    width: 0%;
    height: 2px;
    left: 50%;
    bottom: 0;
    background: royalblue;
    transform: translateX(-50%);
    transition: 0.25s;
}
.inputBox div input:focus + span,
.inputBox div input:valid + span{
    transform: translateY(-30px);
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 1px;
    color: royalblue;
}
.inputBox div input:focus ~ .slider{
    width: 100%;
}
button{
  padding: 1rem;
  background: #28a745;
  border-radius: 15px;
  outline: none;
  border: none;
  color: #fff;
}
.select{
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.6rem;
}
select{
    outline: none;
    margin-top: 0.5rem;
    font-size: 1.6rem;
}
.number{
    font-size: 1.6rem;
}
.number div{
    display: flex;
    justify-content: space-between;
}
.number div input{
    width: 50px;
    text-align: center;
}
</style>